<template>
  <div :class="['zoo-home']">
    <!--<div class="title"><img src="../assets/img/home/zoo-title.png"></div>-->

    <div @click="chooseAnimal(item)" v-if="gradeMark" v-for="(item, index) in animalData" class="abc-img"
      :style="{'left': item.x + 'px', 'top': item.y + 'px', '-webkit-transform': 'rotate('+item.deg+'deg)', 'transform': 'rotate('+item.deg+'deg)'}">
      <img :src="item.src">
    </div>



    <!---->
    <div v-if="beforeGameMark" class="before-start abc-flex-y-center">
      <div class="base-body abc-flex-y-center">
        <div class="content">
          Please select <span class="point">{{ animalName || '' }}</span> in
          <span class="point">{{ second || 0 }}</span> seconds
        </div>

        <div @click="startGame" class="btn">
          Start
        </div>
      </div>
    </div>

    <!---->
    <div v-if="gamingMark" class="before-start abc-flex-y-center">
      <div class="base-body abc-flex-y-center">
        <div class="point-title">{{progress || 1}}</div>
        <div class="content">
          Please select <span class="point">{{ animalName || '' }}</span> in
          <span class="point">{{ second || 0 }}</span> seconds
        </div>

        <div @click="continueGame" class="btn">
          Go
        </div>
      </div>
    </div>

    <!---->
    <div v-if="gameFailMark" class="before-start abc-flex-y-center">
      <div class="base-body abc-flex-y-center">
        <div class="point-title"></div>

        <div @click="restartGame" class="btn">
          Restart
        </div>
      </div>
    </div>

    <!---->
    <div v-if="gradeMark" class="money-par abc-flex-x-center">
      <div class="time">{{second || 0}}</div>

      <div class="abc-flex-x-center">
        <div class="abc-img"><img src="../assets/img/home/money.png"></div>
        <div>{{ moneyCount || 0 }}</div>
      </div>
    </div>

  </div>

</template>

<script>
  import { mapState } from "vuex"
//  import moment from 'moment'
//  import { addUser, updateUser, getUserList, getRandomWords } from '@/api/chooseWord'

  import getRandom from '../assets/js/getRandomArr'
  import { checkpoint, animalNameList } from '../assets/js/fun'



  import '@/assets/js/m'

  export default {
    name: 'Home',
    data(){
      return {
        clientWidth: '', //
        clientHeight: '', //
        title: {}, // 
        usedArea: {}, // 

        progress: '', // (12)
        animalName: '', // 
        animalCount: '', // animal
        second: '', // 
        moneyCount: 10, // 
        animalData: [], // 
        blackList: [], // 

        beforeGameMark: true, // 
        gamingMark: false, // 
        gradeMark: false, // 
        gameFailMark: false, // 

        loopTime: '', // 
      }
    },
    computed: {
      ...mapState([
        'resourceUrl' // host
      ]),

    },
    created () {

      /*
        
        1.   5s10  5s10  5s20  5s20
             5s24  5s24  3s20  3s20
             3s24  3s24  2s24  1s24

             121 + 1 + 2 + 2 + 3 + 3 + 4 + 4 + 5 + 5 + 10 + 20

             
       */

    },
    watch: {

    },
    mounted () {
      // 
      this.start()

      // doc
      doc.init(this)




    },
    methods: {
      // ()
      async start () {
        // 
        this.orientation()

        // zootitle
        this.getTitle()

        // :
        this.confGame(1)

        // 
//        const gameConfig = checkpoint(12)
//        console.log(gameConfig)

//        setInterval(() => {
//          let a = checkpoint(2)
//          console.log(a)
//
//
////          this.progress++
////          console.log(this.progress)
////          this.confGame(5)
//        }, 2000)

      },

      // 
      startGame () {
        this.beforeGameMark = false
        this.gradeMark = true

        // 
        this.moveTime()

      },

      // 
      continueGame () {
        this.gamingMark = false
        this.gradeMark = true

        // 
        this.moveTime()
      },

      // 
      restartGame () {
        this.beforeGameMark = true

        // :
        this.confGame(1)

      },

      // (progress:)
      confGame (progress) {
        // 
        this.progress = progress, // (12)
        this.animalName = '', // 
        this.animalData = [], // 
        this.blackList = [], // 
        this.gradeMark = false // 
        this.gameFailMark = false // 

        // 
        const gameConfig = checkpoint(progress)
        this.animalCount = gameConfig.animalCount
        this.second = gameConfig.second

        // 
        this.renderImg()

      },

      // 
      chooseAnimal (item) {
        console.log(item)
        const name = item.animalName || ''
        console.log(this.animalName)
        if (name === this.animalName) {
          console.log('')

          clearInterval(this.loopTime)

          console.log('progress', this.progress)
          if (this.progress < 12) {
            // 
            this.progress++
            this.confGame(this.progress)

            // 
            this.gamingMark = true

          } else {
            console.log('12')

          }


          console.log(this.progress)
        } else {
          console.log('')
        }

      },

      // 
      renderImg () {
        let aniArr = []
        const randomArr = getRandom.getNorepeatArr(this.animalCount, [1, 24])
        // 
        const ranIndex = getRandom.getNorepeatArr(1, [0, (this.animalCount)])
        this.animalName = animalNameList(randomArr[ranIndex])

        for (let i = 0; i < this.animalCount; i++) {
          // 
          let imgResource = require(`../assets/img/home/icon/icon_${randomArr[i]}.png`)

          // .
          let randomX = ''
          let randomY = ''
          for (let i = 0; true; i++) {
            let currNumX = parseInt(getRandom.getNorepeatArr(1, [0, (this.clientWidth - 80)] )[0])
            let currNumY = parseInt(getRandom.getNorepeatArr(1, [0, (this.clientHeight - 80)] )[0])

            // 
            const mark = this.onlyPosition(currNumX, currNumY)
            if (mark) {
              randomX = currNumX
              randomY = currNumY
              break;
            }
          }
          // .

          let obj = {
            x: parseInt(randomX),
            y: parseInt(randomY),
            deg: getRandom.getNorepeatArr(1, [0, 360])[0],
            src: imgResource,
            animalName: animalNameList(randomArr[i])
          }

          // 
          this.blackList.push({
            x: [obj.x - 80, obj.x + 80],
            y: [obj.y - 80, obj.y + 80]
          })

          aniArr.push(obj)
        }

        this.animalData = aniArr

      },

      // 
      moveTime () {
        this.loopTime = setInterval(() => {
          this.second -= 1

          if (this.second <= 0) {
            clearInterval(this.loopTime)

            // 
            this.gameFailMark = true
          }
        }, 1000)

      },

      // 
      onlyPosition (currNumX, currNumY) {
        let mark = true
        if (this.blackList.length) {
          this.blackList.map((item) => {
            if (currNumX > item.x[0] && currNumX < item.x[1] && currNumY > item.y[0] && currNumY < item.y[1]) {
              mark = false
            }
          })
        }

        return mark

      },

      // 
      orientation () {
        var that = this

        //  CSS3   90 
        let detectOrient = function() {
          let width = document.documentElement.clientWidth,
            height = document.documentElement.clientHeight

          if( width >= height ){ // 

            let style = {
              width: width + 'px',
              height: height + 'px',
              '-webkit-transform': 'rotate(0)',
              'transform': 'rotate(0)',
              '-webkit-transform-origin': '0 0',
              'transform-origin': '0 0'
            }

            $('.zoo-home').css(style)

            // 
            that.clientWidth = width
            that.clientHeight = height

          }
          else{ // 
            let style = {
              width: height + 'px',
              height: width + 'px',
              '-webkit-transform': 'rotate(90deg)',
              'transform': 'rotate(90deg)',
              '-webkit-transform-origin': width / 2 + 'px ' + width / 2 + 'px',
              'transform-origin': width / 2 + 'px ' + width / 2 + 'px'
            }

            $('.zoo-home').css(style)

            // 
            that.clientWidth = height
            that.clientHeight = width

          }

        }
        window.onresize = detectOrient;
        detectOrient();
      },

      // zootitle
      getTitle () {
        const titleWidth = $('.title').outerWidth()
        const titleHeight = $('.title').outerHeight()

        this.title = {
          position: [0, 0],
          width: titleWidth,
          height: titleHeight
        }

        // 
        this.usedArea = {
          x: [(parseInt(this.clientWidth) - parseInt(titleWidth)), parseInt(this.clientWidth)],
          y: [(parseInt(this.clientHeight) - parseInt(titleHeight)), parseInt(this.clientHeight)]
        }

      }
    },
    components: {

    }

  }

  var doc = {
    vueObj: '', // vue

    init (that) {
      this.vueObj = that

      this.helper()
      this.bind()
    },
    helper () {

    },
    bind () {

    },

  }

</script>

<style lang="scss">
  @import "../../../assets/css/pr2rem";
  @import "../assets/css/index";

  .zoo-home {
    position: relative;
    width: 100%;
    height: 100%;
    background: url("../assets/img/home/bg.jpg") no-repeat center / cover;

    .title {
      width: pr(300);
      height: pr(160);
    }

    & > .abc-img {
      position: absolute;
      width: pr(80);
      height: pr(80);
    }

    /**/
    .before-start {
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.5);

      .base-body {
        position: absolute;
        width: pr(350);
        height: pr(200);
        background-color: rgba(255, 255, 255, 0.9);
        border-radius: pr(20);
        padding: pr(10);

        .point-title {
          font-size: pr(20);
        }

        .content {
          font-size: pr(30);
          .point {
            font-size: pr(34);
            color: #F5A623;
          }
        }

        .btn {
          width: pr(150);
          height: pr(50);
          line-height: pr(50);
          background: #F8E71C;
          color: #fff;
          text-align: center;
          border-radius: pr(28);
          font-size: pr(30);
          margin-top: pr(10);
        }

      }
    }

    .money-par {
      position: absolute;
      top: 0;
      right: 0;
      width: pr(80);
      height: pr(50);
      font-size: pr(24);
      color: #fff;
      background: rgba(255, 255, 255, 0.5);
      border-radius: 0 0 0 pr(10);

      .abc-img {
        width: pr(30);
        height: pr(30);
      }
    }

  }

</style>





